.ant-modal {
  max-width: 480px !important;
  min-width: 320px !important;
  width: 100%;
}

.ant-modal .ant-modal-content {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-left: 0;
  padding-right: 0;
}

.ant-modal .ant-modal-content .ant-modal-close {
  display: block;
  position: relative;
  top: 0;
  margin-right: 20px;
}

.ant-modal .ant-modal-content .ant-modal-body {
  width: 100%;
  margin-top: 24px;
  border-top: 1px solid #e6e6e6;
  text-align: center;
}

.ant-modal .ant-modal-title {
  font-family: 'Varela Round', sans-serif;
  font-size: 24px;
  line-height: 32px;
  font-weight: 500;
  margin-left: 30px;
}

.textContain {
  padding: 100px;
}

.ant-list {
  border: 0;
}

.cart-list-box {
  padding: 16px 32px;
}

.cart-list-box .cart-list {
  display: flex;
}

.cart-list-box .middle-box {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.cart-list-box img {
  width: 60px;
  max-width: 100%;
}

.cart-list-box .item-title {
  font-weight: 700;
}

.cart-list-box .item-remove {
  color: #fb416b;
  font-size: 12px;
  line-height: 16px;
  text-decoration: underline;
  cursor: pointer;
}

.cart-list-box .quantity-input {
  width: 90px;
  margin-right: 8px;
  margin-bottom: 12px;
  height: 48px;
  padding-right: 28px;
  padding-left: 28px;
  border: 2px solid #e5e5e5;
  border-radius: 25px;
  background-color: #f8f8f8;
  outline-style: none;
  -webkit-transition: border-color 300ms ease, background-color 300ms ease;
  transition: border-color 300ms ease, background-color 300ms ease;
}

.cart-list-box .quantity-input:focus {
  border-color: #a5c926;
  background-color: #fff;
}

.total-price {
  border-top: 1px solid #e6e6e6;
  display: flex;
  padding: 15px 32px;
  justify-content: space-between;
}

.check-btn {
  height: 48px;
  padding: 12px 28px;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  border-radius: 24px;
  background-color: #a5c926;
  transition: transform 300ms ease, box-shadow 300ms ease, -webkit-transform 300ms ease;
  color: #fff;
  font-size: 14px;
  line-height: 24px;
  font-weight: 600;
  text-align: center;
  cursor: pointer;
}

.check-btn:hover {
  box-shadow: 0 8px 8px -4px rgba(165, 201, 38, 0.5);
  -webkit-transform: scale(1.05);
  -ms-transform: scale(1.05);
  transform: scale(1.05);
}
