.shop-header {
  width: 100%;
  margin-bottom: 40px;
  padding-right: 15px;
  padding-left: 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.shop-header .no-margin {
  margin: 0;
}

.shop-header .arrow-link {
  margin-top: 4px;
  padding-right: 20px;
  background-image: url(https://assets.website-files.com/5badda2935e11303a89a461e/5baf79eb570913b9781a96f2_arrow-right-mini-icon.svg);
  background-position: 100% 50%;
  background-size: 11px;
  background-repeat: no-repeat;
  display: inline-block;
  padding-top: 2px;
  padding-bottom: 2px;
  border-bottom: 2px solid #ddd;
  -webkit-transition: border-color 300ms ease;
  transition: border-color 300ms ease;
  color: #111;
  font-size: 13px;
  line-height: 20px;
  font-weight: 600;
  text-decoration: none;
}

.shop-header .arrow-link:hover {
  border-bottom-color: #a5c926;
}

.shop-header .shop-header-line {
  width: 100%;
  height: 2px;
  margin-top: 32px;
  background-color: #e5e5e5;
}

.shop-header .shop-header-line .shop-header-color {
  width: 136px;
  height: 100%;
  background-color: #a5c926;
}

.full-width {
  width: 100%;
}

.full-width .products {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
}

.full-width .products .product-card-wrapper {
  display: flex;
  width: 25%;
  padding: 15px;
  flex-direction: column;
}

.full-width .products .product-card-wrapper .product-card {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 12px 24px 32px;
  align-items: center;
  border-radius: 16px;
  background-color: #fff;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.15);
  text-align: center;
  transition: transform 300ms ease, box-shadow 300ms ease, -webkit-transform 300ms ease;
  text-decoration: none;
}

.full-width .products .product-card-wrapper .product-card:hover {
  transform: scale(1.05) translate(0px, -2px);
  box-shadow: 0 8px 20px -4px rgba(0, 0, 0, 0.15);
}

.full-width .products .product-card-wrapper .product-card .product-card-image-wrapper {
  display: flex;
  width: 100%;
  height: 200px;
  margin-bottom: 16px;
  padding: 20px;
}

.full-width .products .product-card-wrapper .product-card .product-card-image-wrapper img {
  max-width: 100%;
  vertical-align: middle;
  display: inline-block;
}

.full-width .products .product-card-wrapper .product-card .product-card-heading {
  margin-bottom: 12px;
  color: #111;
  margin-top: 0px;
  font-family: 'Varela Round', sans-serif;
  font-size: 15px;
  line-height: 20px;
  font-weight: 500;
}

.full-width .products .product-card-wrapper .product-card .product-card-price {
  height: 24px;
  padding: 2px 12px;
  border-radius: 16px;
  background-color: #a5c926;
  color: #fff;
  font-size: 12px;
  line-height: 20px;
  font-weight: 500;
}

.shop-categories-wrapper a {
  display: inline-block;
  height: 32px;
  padding: 6px 18px;
  border-radius: 16px;
  color: #111;
  font-size: 13px;
  line-height: 20px;
  text-decoration: none;
  cursor: pointer;
}

.shop-categories-wrapper a:hover {
  text-decoration: underline;
}

.shop-categories-wrapper .filter-btn {
  background-color: #a5c926;
  color: #fff;
}

.shop-categories-wrapper .filter-btn:hover {
  text-decoration: none;
}
